import React from 'react';
import ReactDOM from 'react-dom';

const LogOutModal = ({ isShowing, hide, signOutModal }) => isShowing ? ReactDOM.createPortal(

    <React.Fragment>
        <div className="sign-out-modal">
            <h1>Do you really want to sign out?</h1>
            <div className="row">
                <button style={button} className="sign-out-yes-modal" onClick={signOutModal}>Yes</button>
                <button style={button} className="sign-out-no-modal transparent" onClick={hide}>No</button>
                {/* <a href="#!" className="sign-out-yes-modal" onClick={signOutModal}>Yes</a>
                <a href="#!" className="sign-out-no-modal" onClick={hide}>No</a> */}
            </div>
        </div>
    </React.Fragment>, document.body
) : null;

const button = {
    padding: '1rem',
    borderRadius: '1rem',
    fontSize: '1.5rem',
    width: '7rem',
    textAlign: 'center'
}

export default LogOutModal


